import React from 'react';

const Python1Context = () => {

    function animation1() {
        window.parent.terminal.sendInput("python3 --version\n")
    }

    function animation2() {
        window.parent.terminal.sendInput("python3\n");
        const termWindow = window.parent.document.getElementById("terminal");
        window.parent.terminal.highlightWindow(termWindow);
    }

    function animation3() {
        window.parent.terminal.showTerminal();
        const ele = window.parent.document.getElementById("terminalBarButton4");
        window.parent.terminal.highlightWindow(ele);
    }

    function animation4() {
        window.parent.terminal.sendInput("print(\"Hello, World!\")");
        const ele = window.parent.document.getElementById("terminal");
        window.parent.terminal.highlightWindow(ele);
        const btn = document.getElementById("blurbutton1");
        btn.disabled = true;
        ele.children[0].focus();
    }

    function animation5() {
        window.parent.terminal.sendInput("\n");
        const ele = window.parent.document.getElementById("terminal");
        window.parent.terminal.highlightWindow(ele);
    }

    function animation6() {
        window.parent.terminal.sendInput("help(print)\n");
        const ele = window.parent.document.getElementById("terminal");
        window.parent.terminal.highlightWindow(ele);
    }

    function animation7() {
        window.parent.terminal.sendInput("q\n");
        const ele = window.parent.document.getElementById("terminal");
        window.parent.terminal.highlightWindow(ele);
    }

    function animation8() {
        window.parent.terminal.sendInput("quit()\n");
        window.parent.terminal.sendInput("clear\n");
        window.parent.terminal.showAll();
        const ele = window.parent.document.getElementById("terminalBarButton1");
        window.parent.terminal.highlightWindow(ele);
    }

    function animation9() {
        var commands = [];
        var command = 'rm -f /home/hello_world.py';
        commands.push(command);
        window.parent.terminal.socket.emit('execute', commands);
        setTimeout(function() {
        window.parent.terminal.socket.emit('fs', '/home');
        }, 100);

        /* show dropdown window */
        const eDiv1 = window.parent.document.getElementById("/home");
        const menu = window.parent.document.getElementById("contextMenu");
        const rect = eDiv1.getBoundingClientRect();
        menu.style.left = rect.left + 70 + 'px';
        menu.style.top = rect.top + 12 + 'px';
        console.log('left', menu.style.left, 'top', menu.style.top);
        menu.path = eDiv1.id;
        menu.style.display = 'block';

        const dropdown = window.parent.document.getElementById("newFile");
        const inputBox = window.parent.document.getElementById('inputBox/home');

        setTimeout(function() {
        dropdown.click(null);
        inputBox.children[0].value = "";
        }, 1000);

        setTimeout(function() {
        inputBox.children[0].value = "hello_world.py";
                }, 2000);

        setTimeout(function() {
        commands = [];
        command = 'touch /home/hello_world.py';
            commands.push(command);

            window.parent.terminal.socket.emit('execute', commands);
        inputBox.style.display = 'none';

            /* get a new list */
            window.parent.terminal.socket.emit('fs', '/home');
        },3000);

        setTimeout(function() {
        const ele = window.parent.document.getElementById("/home/hello_world.py");
                    window.parent.terminal.highlightWindow(ele);
                }, 3500);
    }

    function animation10() {
        window.parent.terminal.socket.emit('file', "/home/hello_world.py");
        const ele = window.parent.document.getElementById("editor1");
        window.parent.terminal.highlightWindow(ele);
    }

    function animation11() {
        window.parent.terminal.editor.setValue("print(\"Hello, World!\")", 1);
        const ele = window.parent.document.getElementById("editor1");
        window.parent.terminal.highlightWindow(ele);

        const filePath = window.parent.terminal.editor.filePath;
        const content = window.parent.terminal.editor.getValue();
        window.parent.terminal.socket.emit('saveFile', {path:filePath, content:content});
    }

    function animation12() {
        window.parent.terminal.sendInput("cd /home\n");
        setTimeout(function() {
            window.parent.terminal.sendInput("python3 hello_world.py\n");
        }, 1000);

        const ele = window.parent.document.getElementById("terminal");
        window.parent.terminal.highlightWindow(ele);

    }

    function animation13() {
        window.parent.terminal.sendInput("python3\n");
        setTimeout(function() {
                window.parent.terminal.sendInput("3+3\n");
        }, 500);

        setTimeout(function() {
                        window.parent.terminal.sendInput("(84949*292)/322\n");
                }, 1500);

        setTimeout(function() {
                        window.parent.terminal.sendInput("quit()\n");
                }, 1600);

        const ele = window.parent.document.getElementById("terminal");
        window.parent.terminal.highlightWindow(ele);
    }


    function animation14() {
        window.parent.terminal.socket.emit('file', "/home/variables.py");
        const ele = window.parent.document.getElementById("editor1");
        window.parent.terminal.highlightWindow(ele);
    }

    function animation15() {
        window.parent.terminal.socket.emit('file', "/home/datatypes.py");
        const ele = window.parent.document.getElementById("editor1");
        window.parent.terminal.highlightWindow(ele);
    }

    function animation16() {
        window.parent.terminal.sendInput("cd /home\n");
        setTimeout(function() {
                window.parent.terminal.sendInput("python3 datatypes.py\n");
        }, 1000);

        const ele = window.parent.document.getElementById("terminal");
        window.parent.terminal.highlightWindow(ele);
    }

    function animation17() {
        window.parent.terminal.socket.emit('file', "/home/strings.py");
        const ele = window.parent.document.getElementById("editor1");
        window.parent.terminal.highlightWindow(ele);
    }

    function animation18() {
        window.parent.terminal.sendInput("cd /home\n");
        setTimeout(function() {
                window.parent.terminal.sendInput("python3 strings.py\n");
        }, 1000);

        const ele = window.parent.document.getElementById("terminal");
        window.parent.terminal.highlightWindow(ele);
    }




  return (
    <div className="w-full h-full text-lg">
        <div className='mb-2'>
                <h1 className='text-2xl font-bold border-b-4 border-purple-400 tracking-wider inline'>1. Python Programming in 15 minutes</h1>
        </div>
        <div className='flex flex-col'>
                <h2 className='text-xl font-bold'>
                        1.1. About Python
                </h2>
                <div className='mt-1 ml-2'>
                Python 3.0, also known as Python 3000, is an easy-to-learn, powerful programming language. 
                It has efficient high-level data structures and a simple but effective approach to object-oriented programming (OOP). 
                Python’s elegant syntax and dynamic typing, together with its interpreted nature, 
                make it an ideal language for scripting and rapid application development in many areas on most platforms.
                <br/><br/>
                In this introductory article, we will learn two basic modes to program/run Python programs as well as main Python syntax.
                </div>
        </div>

        <hr className='w-full border my-4 border-gray-300'/>
        <div className='flex flex-col'>
            <h2 className='text-xl font-bold'>
                1.2. Install Python
            </h2>
            <div className='mt-1 ml-2'>
                Installing or updating Python on your computer is the first step to becoming a Python programmer. 
                There are a multitude of installation methods: you can download official Python distributions from Python.org, 
                install from a package manager, and even install specialized distributions for scientific computing, 
                Internet of Things, and embedded systems. Please refer to xxx for the detailed installation on a Linux machine.<br/><br/>
                We have installed Python in your <b>practice machine</b> beforehand -- 
                you can interact with your practice machine via the communication interface on the right-hand side. <br/>

                Make sure, you powered on the practice machine before moving forward.
			    The interface of the practice machine has three "windows":
                <ul className='list-disc ml-6'>
				  <li> <b>Explorer</b> (left): It lists all files/folders, which you can add/delete/open. </li>
				  <li> <b>Editor</b> (right-top): When you open a file, the content of the file will be displayed in Editor, where you can view/write. </li>
				  <li> <b>Terminal</b> (right-bottom): It provides a command-line interface, where you can write down <code>shell</code> commands.</li>
                </ul>
                <br/>
                Since we have installed Python, you can check for the Python version by typing the following command in <b>Terminal</b>:
                <div className="lesson_high"><code>python3 --version</code>
                </div>
                <button className="lesson_button" onClick={animation1}><span>Show it</span></button>
                
            </div>



        </div>

        <hr className='w-full border my-4 border-gray-300'/>

        <div className='flex flex-col'>
            <h2 className='text-xl font-bold'>
                1.3.1. Using Python Shell
            </h2>
            <div className='mt-1 ml-2'>
                To open the Python shell, type in "python3" in <b>Terminal</b>, 
                this will begin an interactive python session. 
                This interactive session doesn't save any of your code in a file; 
                it expires as soon as you exit from the shell.<br/>
                Now let's write the code to print <code>Hello World</code> in the Python shell. 
                First, enter "python3" in <b>Terminal</b>. 
                <div className="block lesson_high"><code>python3</code>
                </div>
                <button className="lesson_button" onClick={animation2}><span>Do it</span></button>
                Notice that, you can choose to maximize the terminal window for the following practice.
                <button className="lesson_button" onClick={animation3}><span>Maximize it</span></button>
                Now, write down the following code to pring <code>Hello World</code> in the Python shell.
                <div className="lesson_high"><code>print("Hello, World!")</code></div>
                <button className="lesson_button" onClick={animation4}><span>Write it</span></button>
                As soon as you hit "enter" button (in <b>Terminal</b>),
                the Python interpreter runs the code and gives the output.
                <button className="lesson_button" onClick={animation5}><span>Do it</span></button>
                Note that, python shell is typically used to test programs on the go. 
                You can type down help(anything) for "anything"'s documentation. 
                Here, "anything" is any syntax in Python, such as the <code>print</code> function we just saw.
                It's way faster than searching any web. 
                For example, you can check the documentation for the <code>print</code> function.
                <div className="lesson_high"><code>help(print)</code></div>
                <button className="lesson_button" onClick={animation6}><span>Check documentation of print</span></button>
                To quit from the help information, press q.
                <button className="lesson_button" onClick={animation7}><span>Quit documentation</span></button>
                Finally, we can quit the python shell by typing down Ctrl-D or quit().
                <button className="lesson_button" onClick={animation8}><span>Quit shell</span></button>
            </div>
        </div>

        <hr className='w-full border my-4 border-gray-300'/>

        <div className='flex flex-col'>
            <h2 className='text-xl font-bold'>
            1.3.2. Using Python Script
            </h2>
            <div className='mt-1 ml-2'>
                The second and more common way to run Python programs is to use Python scripts. 
                You edit and save your Python code in a <code>.py</code> file
                and in <b>Terminal</b> use <code>python3 file_name.py</code> to run the program.
                <br/>
                For example, you can create an empty <code>hello_world.py</code> file under the <code>home</code> directory 
                (in <b>Explorer</b>).
                <button className="lesson_button" onClick={animation9}><span>Create hello_world.py</span></button>
                The newly created python file is stored under <code>/home</code>. 
                To edit it, you need to first open it --- by clicking on the file name in <b>Explorer</b>.
                <button className="lesson_button" onClick={animation10}><span>Open script</span></button>
                As the file is empty, there is nothing shown from <b>Editor</b> (only the default placeholder).
                Now, you can write <code>print("Hello, World!")</code> in the file editor. 
                Save any modification by clicking on the <i className="fa fa-sticky-note-o"></i> button in <b>Editor</b>'s upper tool bar. 
                <button className="lesson_button" onClick={animation11}><span>Do it</span></button>
                Congratulations! You just wrote your first python script. 
                You will find that when you are writing large pieces of Python code, you can only use Python script Mode.
                To execute/run Python scripts, in <b>Terminal</b>, type down the following command.
                <div className="lesson_high"><code>cd /home/</code></div>
                <div className="lesson_high"><code>python3 hello_world.py</code></div>
                <button className="lesson_button" onClick={animation12}><span>Run it</span></button>
            </div>
        </div>


        <hr className='w-full border my-4 border-gray-300'/>
        <div className='flex flex-col'>
            <h2 className='text-xl font-bold'>
            1.4. Python Syntax
            </h2>
            <div className='mt-1 ml-2'>
                To program in any language, we need to know its syntax.
                Syntax refers to the rules that define the structure of a language, 
                i.e., the rules that control the structure of the symbols, punctuation, and words of a programming language.
                For Python, its interpreters execute programs at runtime. The incorrect syntax Python programs will cause the code to fail.
                Hence, it is crucial that a programmer pays close attention to a language's syntax.
                <br/><br/>
                Learning syntax for any language can seem daunting, with so many little details to keep in mind. 
                In the beginning, it may be slow going as you become more familiar with the language.
                However, the following interactive learning will increase the learning efficiency.
            </div>
        </div>


        <hr className='w-full border my-4 border-gray-300'/>

        <div className='flex flex-col'>
            <h2 className='text-xl font-bold'>
            1.4.1. Number and Variables
            </h2>
            <div className='mt-1 ml-2'>
                As we know numbers are everywhere, so let's see how Python deals with numbers.
                First, Python shell can be used as a calculator; you can perform the basic mathematical operation in python shell such as addition, division, etc.
                <div className="lesson_high"><code>&gt;&gt;&gt; 3+3</code></div>
                <div className="lesson_high"><code>6</code></div>
                <div className="lesson_high"><code>&gt;&gt;&gt; (84949*292)/322</code></div>
                <div className="lesson_high"><code>77034</code></div>
                <button className="lesson_button" onClick={animation13}><span>Do it</span></button>
                In Python we have three distinct number types 
                they are integers, floating point numbers, and complex numbers. 
                Integers are numbers with no fractional part they can be either positive or negative or unsigned(zero).
                Float values are numbers with a fractional part.
                <br/><br/>
                Further, in programming, a variable is nothing more than a name for something. Variable names are case sensitive.
                To declare a variable in Python, you have to give its name followed by an equal sign and the initialisation value.
                <button className="lesson_button" onClick={animation14}><span>Open Example</span></button>
                When you keep assigning the values to the same variable name Python simply replaces the older value with newer value.
                <br/><br/>
                In addition to the Number type, Python3 also supports two other basic data types: Boolean and String.
                In total, Python supports the following basic data types:
                <ul className='list-disc ml-6'>
                    <li>Boolean</li>
                    <li>Integer</li>
                    <li>Float</li>
                    <li>Complex</li>
                    <li>String</li>
                </ul>
                <button className="lesson_button" onClick={animation15}><span>Open Another Example</span></button>
                You can run this Python example using the same way as above:
                <div className="lesson_high"><code>cd /home/</code></div>
                <div className="lesson_high"><code>python3 datatypes.py</code></div>
                <button className="lesson_button" onClick={animation16}><span>Run it</span></button>
            </div>
        </div>

        <hr className='w-full border my-4 border-gray-300'/>

        <div className='flex flex-col'>
            <h2 className='text-xl font-bold'>
            1.4.2. Strings
            </h2>
            <div className='mt-1 ml-2'>
                In Python, string is a sequence of characters enclosed in either single or double quotes.
                Strings are immutable sequence of Unicode points.

                <button className="lesson_button" onClick={animation17}><span>Open Example</span></button>
                You can run this Python example:
                <div className="lesson_high"><code>cd /home/</code></div>
                <div className="lesson_high"><code>python3 strings.py</code></div>

                <button className="lesson_button" onClick={animation18}><span>Run it</span></button>

            </div>
        </div>

    </div>
  );
};

export { Python1Context as default}